.loginBox{
    text-align: left;
    width:310px;
    height: 500px;
    margin-top: 10%;
    padding: 40px;
    position: absolute;
    top: 25%; 
    left: 50%;
     -webkit-transform: translate(-50%,-50%);
             transform: translate(-50%,-50%);
     /* text-align: center; */
    
    
        border: 1px solid rgb(153, 153, 153);
        border-radius: 20px;
        box-shadow: 10 10px 12px -1px black;
        background-color: #f1f1f127;
        /* box-shadow: .8px .9px 3px black;  */
        -webkit-transition:  box-shadow .5s ease-out;
        transition:  box-shadow .5s ease-out;
     
    }

    .loginBox:hover{
        box-shadow: 40px 40px 60px black; 
        -webkit-transition:  box-shadow .4s ease-in; 
        transition:  box-shadow .4s ease-in;
    }
    
    .loginBox input[type="email"], .loginBox input[type="password"]{
       border:0;
       background: rgba(255, 255, 255, 0.5);
       display: block;
       /* margin: 20px auto; */
       margin-top: 0%;
       text-align: left;
       border: 2px solid #3498db; 
       outline: none;
       width: 100%;
       height: 40px;
       color: white;
       border-radius: 5px;
       -webkit-transition: 0.25s;
       transition: 0.25s;
       font-weight: 500;
    }

    .loginBox input[type="email"]:hover, .loginBox input[type="password"]:hover{ 
        border-color:darkblue;
        background: white;
        color: black;
     }
    
    .loginBox button[type="submit"]{
        /* padding: 14px 40px; */
        background: #2ecc71;
        display: block;
        margin: 20px auto;
        text-align: center;
        border: 2px solid #2ecc71; 
        outline: none;
        width: 100px;
        height: 40px;
        color: white;
        text-align: center;
        border-radius: 5px;
        -webkit-transition: 0.25s;
        transition: 0.25s;
        font-weight: 500;
        cursor: pointer;
     }

     .loginBox button[type="submit"]:hover{
        border:0;
        /* padding: 14px 40px; */
        background: #1c8d4b;
        display: block;
        margin: 20px auto;
        text-align: center;
        border: 2px solid #1c8d4b; 
        outline: none;
        width: 110px;
        height: 40px;
        color: white;
        border-radius: 5px;
        -webkit-transition: 0.25s;
        transition: 0.25s;
        font-weight: 500;
        cursor: pointer;
     }
    
    
